.swiper-container {
    width: 100%;
    height: 100%;
    margin-right: 0;
    @include media-breakpoint-up(lg){
        width: 92%;
    }
}
.swiper-slide {
    @include media-breakpoint-up(md){
        width: 100%;
    }
}
.swiper-img {
    display: block;
    @include img-cover(100%, 350px)
}